@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css?family=Hind&display=swap);
*{font-family:Helvetica, Arial, sans-serif;margin:0px}html{height:100vh;width:100vw}main{width:100%}.mt-1{margin-top:5px}.mt-5{margin-top:20px}.mb-1{margin-bottom:5px}.mb-5{margin-bottom:20px}.pointer{cursor:pointer}*{-webkit-font-smoothing:antialiased;color:#acbdce}:root{--border-radius: 10px}body,html{background:#5f8ac7;display:grid;height:100%;place-items:center}.CardSearch{padding:1px;border-radius:10px;border-radius:var(--border-radius);background:linear-gradient(-67deg, rgba(146,187,224,0.7), rgba(255,255,255,0.8));overflow:hidden;width:380px;margin:auto}.CardInner{padding:16px 16px;background-color:#e2e9f4;border-radius:10px;border-radius:var(--border-radius)}.container{display:flex}.Icon{min-width:46px;min-height:46px;display:flex;align-items:center;justify-content:center;border-radius:10px;border-radius:var(--border-radius);margin-right:12px;box-shadow:-2px -2px 6px rgba(255,255,255,0.6),2px 2px 12px #c8d8e7}.Icon svg{-webkit-transform:translate(-1px, -1px);transform:translate(-1px, -1px)}label{font-family:"Hind", sans-serif;display:block;color:#3c4b66;margin-bottom:12px;background:linear-gradient(45deg, #6b7b8f, #3c4b66);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.InputContainer{width:100%}input{background-color:#e3edf7;padding:16px 32px;border:none;display:block;font-family:'Orbitron', sans-serif;font-weight:600;color:#a9b8c9;-webkit-appearance:none;transition:all 240ms ease-out;width:100%}input::-webkit-input-placeholder{color:#6d7f8f}input:-ms-input-placeholder{color:#6d7f8f}input::placeholder{color:#6d7f8f}input:focus{outline:none;color:#6d7f8f;background-color:#eff5fa}.InputContainer{--top-shadow: inset 1px 1px 3px #c5d4e3, inset 2px 2px 6px #c5d4e3;--bottom-shadow: inset -2px -2px 4px rgba(255,255,255, .7);position:relative;border-radius:10px;border-radius:var(--border-radius);overflow:hidden}.InputContainer:before,.InputContainer:after{left:0;top:0;display:block;content:"";pointer-events:none;width:100%;height:100%;position:absolute}.InputContainer:before{box-shadow:var(--bottom-shadow)}.InputContainer:after{box-shadow:var(--top-shadow)}.btns{background-color:#e2e9f4;border-style:none;border-radius:10px;color:#3c4b66;display:block;margin:auto;margin-top:10px;padding:12px 32px;text-align:center;text-decoration:none;font-family:'Orbitron', sans-serif;font-size:16px}.cards{display:flex;flex-wrap:wrap;justify-content:space-between}.card{margin:20px;padding:20px;width:500px;min-height:200px;align-items:flex-start;flex-direction:column;border-radius:10px;box-shadow:0px 6px 10px rgba(0,0,0,0.25);transition:all 0.2s}.card:hover{box-shadow:0px 6px 10px rgba(0,0,0,0.4);-webkit-transform:scale(1.1);transform:scale(1.1)}.card__link,.card__exit,.card__icon{text-decoration:none;color:rgba(255,255,255,0.9)}.card__icon{position:absolute}.card__title{padding-left:100px;font-weight:400;color:#ffffff}.spn{height:2px;width:200px;background-color:#fff;margin-left:100px;margin-bottom:10px}.card__temp{padding-left:100px;font-weight:400;color:#ffffff;font-size:medium}.card__status{padding-left:100px;font-size:medium;font-weight:400;color:#ffffff}.card__apply{align-self:center}.card-1{background:radial-gradient(#5FB5DE, #5F8AC7)}.card-2{background:radial-gradient(#fbc1cc, #fa99b2)}.card-3{background:radial-gradient(#76b2fe, #b69efe)}.card-4{background:radial-gradient(#60efbc, #58d5c9)}.card-5{background:radial-gradient(#f588d8, #c0a3e5)}@media (max-width: 1600px){.cards{justify-content:center}}

